import { Component, Event, EventEmitter, h, Prop } from '@stencil/core';
import { getLevel1Items } from '../../services/menu-items-service';
import { dealerSearchResultsStore } from '../../state/dealer-search-results-store';
import { navContentStore } from '../../state/nav-content-store';
import { navStateStore } from '../../state/nav-state-store';
import { MenuItemVariant } from '../../utility/constants';
import { isDesktop } from '../../utility/helper';
import { isShopAvailable } from '../../utility/helpers/shop-helper';
import { goTo } from '../../services/routing-manager';

@Component({
  tag: 'phn-level-1',
  styleUrl: 'level-1.scss',
  scoped: true
})
export class Level1 {
  @Prop() locale!: string;

  @Prop() breakpoint!: number;

  @Event({ bubbles: true }) mobileMenuItemClick!: EventEmitter<string[]>;

  private showRightIconRight(iconRight: string | undefined): string | undefined {
    const { app } = navStateStore.state;
    const isOneUiMobileShop = isShopAvailable(this.locale, app) && !isDesktop(this.breakpoint);

    if (iconRight !== undefined && isOneUiMobileShop) {
      iconRight = 'arrow-head-right';
    }
    return iconRight;
  }

  render(): HTMLPhnLevel1Element {
    const { loggedInState, unreadMessagesCount, userConsent } = navStateStore.state;
    const level1Items = getLevel1Items(
      navContentStore.state,
      dealerSearchResultsStore.state,
      this.locale,
      loggedInState,
      unreadMessagesCount,
      userConsent
    );

    return (
      <nav>
        {level1Items.map(({ routingKeys, elementId, iconRight, showIconModifier, ...level1Item }) => (
          <phn-menu-item
            key={elementId}
            elementId={elementId}
            onClick={() => {
              if (level1Item.link === undefined) {
                goTo(...routingKeys);
              }
            }}
            iconRight={iconRight !== undefined ? this.showRightIconRight(iconRight) : 'arrow-head-right'}
            variant={MenuItemVariant.Level1}
            {...level1Item}
          >
            {showIconModifier ? (
              <span
                slot="icon-modifier"
                aria-hidden="true"
                class="myporsche-unread-messages-indicator"
                data-test-id="unread-messages-indicator"
              />
            ) : null}
          </phn-menu-item>
        ))}
      </nav>
    );
  }
}
